import { deletePageData, getPageListData, editPageData, createPageData } from '@/service/main/system/system';
const system = {
    namespaced: true,
    state: () => {
        return {
            usersCount: 0,
            usersList: [],
            roleCount: 0,
            roleList: [],
            goodsCount: 0,
            goodsList: [],
            menuCount: 0,
            menuList: [],
            departmentList: [],
            departmentCount: 0,
            categoryList: [],
            categoryCount: 0
        };
    },
    getters: {
        pageListData(state) {
            return (pageName) => {
                return state[`${pageName}List`];
            };
        },
        pageListCount(state) {
            return (pageName) => {
                return state[`${pageName}Count`];
            };
        }
    },
    mutations: {
        changeUsersList(state, userList) {
            state.usersList = userList;
        },
        changeUsersCount(state, userCount) {
            state.usersCount = userCount;
        },
        changeRoleList(state, roleList) {
            state.roleList = roleList;
        },
        changeRoleCount(state, roleCount) {
            state.roleCount = roleCount;
        },
        changeGoodsList(state, goodsList) {
            state.goodsList = goodsList;
        },
        changeGoodsCount(state, roleCount) {
            state.goodsCount = roleCount;
        },
        changeMenuList(state, menuList) {
            state.menuList = menuList;
        },
        changeMenuCount(state, menuCount) {
            state.menuCount = menuCount;
        },
        changeDepartmentList(state, departmentList) {
            state.departmentList = departmentList;
        },
        changeDepartmentCount(state, departmentCount) {
            state.departmentCount = departmentCount;
        },
        changeCategoryList(state, categoryList) {
            state.categoryList = categoryList;
        },
        changeCategoryCount(state, categoryCount) {
            state.categoryCount = categoryCount;
        }
    },
    actions: {
        async getPageListAction({ commit }, payload) {
            const { pageName, queryInfo } = payload;
            const url = `/${pageName}/list`;
            const pageResult = await getPageListData(url, queryInfo);
            const { list, totalCount } = pageResult.data;
            const pageNameUp = pageName.slice(0, 1).toUpperCase() + pageName.slice(1);
            commit(`change${pageNameUp}List`, list);
            commit(`change${pageNameUp}Count`, totalCount);
        },
        async deletePageDataAction({ commit }, payload) {
            console.log(commit);
            const { pageName, id } = payload;
            const pageUrl = `/${pageName}/${id}`;
            await deletePageData(pageUrl);
        },
        async editPageDataAction({ dispatch }, payload) {
            const { pageName, editData, id } = payload;
            const pageUrl = `/${pageName}/${id}`;
            const res = await editPageData(pageUrl, editData);
            console.log(res);
            dispatch('getPageListAction', {
                pageName,
                queryInfo: {
                    offset: 0,
                    size: 10
                }
            });
        },
        async createPageDataAction({ dispatch }, payload) {
            const { pageName, createData } = payload;
            const pageUrl = `/${pageName}`;
            await createPageData(pageUrl, createData);
            dispatch('getPageListAction', {
                pageName,
                queryInfo: {
                    offset: 0,
                    size: 10
                }
            });
        }
    }
};
export default system;
