import hdRequest from '../index';
var LoginAPI;
(function (LoginAPI) {
    LoginAPI["AccountLogin"] = "/login";
    LoginAPI["UserInfo"] = "/users/";
    LoginAPI["UserMenus"] = "/role/";
})(LoginAPI || (LoginAPI = {}));
export function accountLoginRequest(account) {
    return hdRequest.post({
        url: LoginAPI.AccountLogin,
        data: account,
        showLoading: false
    });
}
export function userInfoRequest(id) {
    return hdRequest.get({
        url: LoginAPI.UserInfo + id,
        showLoading: false
    });
}
export function userMenusByRoleIdRequest(id) {
    return hdRequest.get({
        url: LoginAPI.UserMenus + id + '/menu'
    });
}
